@import "../../global.scss";

.portfolio {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  @include mobileExtraSmall {
    /* background-color: greenyellow; */
  }

  h1 {
    margin-top: 10px;
    font-size: 40px;
    @include mobileExtraSmall {
      font-size: 2rem;
      height: 15rem;
    }

    @include mobileSmall {
      font-size: 2.2rem;
      height: 15rem;
    }

    @include mobile {
      font-size: 2rem;
      margin-bottom: 10px;
    }
  }

  ul {
    /* margin: 10px; */
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @include mobileExtraSmall {
      /* flex-wrap: wrap; */
      min-width: 60%;
      max-width: 80%;
      height: 10rem;
      /*  border: 2px solid red; */
      display: flex;
      justify-content: space-between;
    }
    @include mobileSmall {
      /* background-color: rebeccapurple; */
    }

    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
    }

    li {
      @include mobileExtraSmall {
        width: 100%;
        /* background-color: rgb(47, 248, 255); */
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
      }
      @include mobileSmall {
        height: 1.2rem;
      }
      @include mobile {
        margin-bottom: 5px;
        font-size: 1.5em;
      }
    }
  }

  .container {
    width: 90%;
    @include flexCenter;
    flex-wrap: wrap;
    @include mobileExtraSmall {
      width: 100%;
    }
    @include mobileSmall {
      /* background-color: yellow; */
      width: 100%;
    }
    @include mobile {
      width: 100%;
    }

    .item {
      border: 1px solid rgb(224, 224, 224);
      @include flexCenter;
      width: 400px;
      height: 280px;
      margin: 10px 20px;
      border-radius: 20px;
      color: white;
      position: relative;
      transition: all 0.5s ease;
      cursor: pointer;
      @include mobile {
        width: 200px;
        height: 130px;
      }
      @include mobileSmall {
      }
      a {
        height: 100%;
        width: 100%;
        overflow: hidden;
        position: absolute;
        left: 0;
      }
      h3 {
        position: absolute;
        font-size: 2em;
        pointer-events: none;
        @include mobile {
          text-align: center;
        }
      }
      img {
        object-fit: cover;
        width: 100%;
        height: 100%;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.3;
          z-index: 0;
          cursor: pointer;
        }
      }
    }
  }
}
